@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@300;400&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.column {
    width: 33.3%;
    float: left;
}

.image {
    background: #000000;
    position: relative;
    flex: 1 1;
    max-width: 460px;
    height: 360px;
    margin: 5px;
    transition: 0.6s;
    overflow: hidden;
}

.image:hover {
    border-radius: 5px;
    box-shadow: 0px 0px 0px 5px rgba(0, 0, 0, 0.25);
}

.image img {
    opacity: 0.8;
    position: relative;
    vertical-align: top;
    transition: 0.6s;
    transition-property: opacity;
    width: 100%;
    height: 100%;
}

.image:hover img {
    opacity: .2;
}

.image .details {
    position: absolute;
    top: 0;
    right: 0;
    color: #ffffff;
    width: 100%;
    height: 100%;
}

.image .details h2 {
    padding-left: 30px;
    padding-right: 30px;
    text-align: center;
    font-size: 35px;
    text-transform: uppercase;
    font-weight: 300;
    margin-top: 300px;
    transition: 0.4s;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
}

.image .details h2 span {
    font-weight: 900;
}

.image:hover .details h2 {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px);
}

.image .details p {
    margin: 30px 30px 0 30px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    opacity: 0;
    transition: 0.6s;
    transition-property: opacity, -webkit-transform;
    transition-property: opacity, transform;
    transition-property: opacity, transform, -webkit-transform;
}

.image:hover .details p {
    opacity: 1;
    -webkit-transform: translateY(-110px);
            transform: translateY(-110px);
}

.more {
    position: absolute;
    background: rgba(255, 255, 255, 0.8);
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    bottom: -60px;
    transition: 0.6s;
    transition-property: bottom;
}

.image:hover .more {
    bottom: 0;
}

.more .read-more {
    color: #000;
    text-decoration: none;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
}

.more .read-more span {
    font-weight: 900;
}

/* Extra CSS */

.footer {
    text-align: center;
}

.footer .col {
    width: 100%;
    background-color: #070617;
    bottom: 0;
    height: auto;
    float: left;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    padding: 0px 20px 20px 20px;
}

.footer .col h1 {
    margin: 0;
    padding: 0;
    font-family: inherit;
    font-size: 12px;
    line-height: 17px;
    padding: 20px 0px 5px 0px;
    color: rgba(255, 255, 255, 0.2);
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 0.250em;
}

.footer .col ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.footer .col ul li a {
    color: #ffffffff;
    transition: .2s;
    text-decoration: none;
}

.footer .col ul li a:hover {
    color: rgb(255, 174, 0);
}

.footer .col ul li {
    color: #999999;
    font-size: 14px;
    font-family: inherit;
    font-weight: bold;
    padding: 5px 0px 5px 0px;
    cursor: pointer;
    transition: .2s;
    -webkit-transition: .2s;
    -moz-transition: .2s;
}

.footer .col ul li:hover {
    color: #ffffff;
    transition: .1s;
    -webkit-transition: .1s;
    -moz-transition: .1s;
}

.clearfix {
    clear: both;
}

.column {
    text-align: -webkit-center;
}

@media only screen and (min-width : 320px) {
    .column {
        width: 100%;
        float: left;
    }
}

/* Extra Small Devices, Phones */
@media only screen and (min-width : 480px) {
    .column {
        width: 100%;
        float: left;
    }
}

/* Small Devices, Tablets */
@media only screen and (min-width : 768px) {
    .column {
        width: 50%;
        float: left;
    }
}

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {
    .column {
        width: 50%;
        float: left;
    }
}

/* Large Devices, Wide Screens */
@media only screen and (min-width : 1200px) {
    .column {
        width: 33.3%;
        float: left;
    }
}
body {
    background-image: linear-gradient(to left, #F7971E, #FFD200);
    /* background-color: #070617; */
}

.loader {
    border: 10px solid #f3f3f3;
    border-radius: 50%;
    border-top: 10px solid #33ff00;
    margin-top: 20%;
    width: 60px;
    height: 60px;
    -webkit-animation: spin 2s linear infinite;
    /* Safari */
    animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.wholecontainer {
    background-size: cover;
    text-align: -webkit-center;
}

.container {
    height: auto;
    padding-bottom: 399px;
}

/* Add a black background color to the top navigation */
.topnav {

    height: 50px;
    background-color: #333;
    padding: 15px;
    overflow: hidden;
    font-weight: bold;
}

/* Style the links inside the navigation bar */
.topnav .active {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 25px;
    transition: 0.5s;
}

/* Change the color of links on hover */
.topnav .active:hover {
    color: rgb(255, 174, 0);
}


/* Hide the link that should open and close the topnav on small screens */
.topnav .btn {
    display: none;
    float: right;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    transition: 0.5s;
    border-radius: 5px;
}

.topnav .btn:hover {
    color: rgb(255, 174, 0);
    box-shadow: 0px 0px 0px 2.5px rgba(255, 174, 0, 0.979);
}

/* When the screen is less than 600 pixels wide */
@media screen and (max-width: 600px) {
    .topnav a.btn {
        color: #f2f2f2;
        float: right;
        display: block;
    }

    .container {
        height: 205vh;
        text-align: -webkit-center;
    }
}

.instruction {
    top: 70%;
    width: 50%;
    border-radius: 10px;
    padding: 15px;
    font-size: 20px;
    font-weight: 700;
    border: 1px solid white;
    margin: 20% auto 0;
    background: linear-gradient(045deg, rgba(12, 11, 12, 0.555), rgba(59, 57, 57, 0.555));
    color: white;
}
